.Detail-View {
    padding: 10px;
}

.Detail-View-Main-Content {
    padding: 10px;
    border-radius: 5px;
    border: 1px solid #eaeaea;
    display: grid;
    grid-template-columns: 30% 70%;
}

.Detail-View-Main-Content-Column1 {
    float: left;
    display: inline;
    padding: 5px;
}

.Detail-View-Main-Content-Column2 {
    float: right;
    display: inline;
    border-left: 1px solid #eaeaea;
}

.Detail-View-Main-Content-Column2 {
    width: 69%;
    float: right;
    display: inline;
    border-left: 1px solid #eaeaea;
}

.Detail-View-Main-Content-Column2 > .container {
    padding: 5px;
}

.Detail-View-Main-Content-Column2 > .container > .stat {
    /*padding: 5px;*/
    border-bottom: 1px solid #eaeaea;
}

.Detail-View-Main-Content-Column2 > .container > .no-border {
    /*padding: 5px;*/
    border-bottom: 0px solid #eaeaea;
}

.Detail-View-Main-Content-Column2 > .container > .Row1 {
    padding: 5px;
}

.Detail-View-Main-Content-Column2 > .container > .information {
    padding-left: 10px;
    font-size: 14px;
    font-family: 'Roboto', sans-serif;
    padding-bottom: 10px;
}

.Detail-View-Main-Content-Column2 > .container > .stat > .Detail-View-Main-Content-split {
    min-width: 100px;
    max-width: 200px;
    padding-right: 30px;
}

.Detail-View-Main-Content-Column2 > .container > .stat > .Detail-View-Main-Content-split > .information-heading {
    padding-left: 10px;
    font-size: 14px;
    font-family: 'Lexend Deca', sans-serif;
    padding-bottom: 10px;
}

.Detail-View-Main-Content-Column2 > .container > .stat > .Detail-View-Main-Content-split > .information-val {
    padding-left: 10px;
    font-size: 12px;
    font-family: 'Roboto', sans-serif;
    padding-bottom: 10px;
}

.Detail-View-Main-Content-Name {
    font-family: 'Lexend Deca', sans-serif;
    font-size: 16px;
}

.Detail-View-Main-Content-Name > span {
    padding-left: 3px;
    padding-right: 3px;
    border-radius: 3px;
    border: 1px solid #ea180e;
    font-size: 8px;
    margin-right: 10px;
    margin-top: 10px;
    font-family: 'Lexend Deca', sans-serif;
}

.Detail-View-Main-Content-Name-underline {
    text-decoration: underline;
    cursor: pointer;
    color: #3498db;
}

.Detail-View-Main-Content-Id {
    font-size: 14px;
    /*float: right;*/
    font-family: 'Roboto', sans-serif;
}

.Detail-View-Main-Content-Id > .image {
    width: 16px;
    float: left;
    margin-top: 1px;
}

.Detail-View-Main-Content-split {
    display: inline-block;
    vertical-align: top;
}

.Detail-View-Main-Content-Body {
    width: 100%;
    /*white-space:nowrap;*/
}

.Detail-View-Main-Content-Body > .stock-list-heading {
    font-family: 'Lexend Deca', sans-serif;
    font-size: 16px;
}

.Detail-View-Main-Content-Body > .stock-list {
    /*overflow-x: scroll;*/
    display: grid;
    grid-template-columns: 25% 25% 25% 25%;
    grid-template-rows: auto;

}

.Detail-View-Main-Content-Body > .stock-list > .stock-list-item {
    /*display: inline-block;*/
    padding: 10px;
    margin-right: 10px;
    margin-top: 10px;
    margin-bottom: 10px;
    border-radius: 3px;
    /*border: 1px solid #eaeaea;*/
    min-width: 150px;
}

.Detail-View-Main-Content-Body > .stock-list > .stock-list-item > .stock-list-item-id {
    font-size: 10px;
    font-family: 'Roboto', sans-serif;
    padding: 3px;
    margin-bottom: 5px;
    border: 1px solid #eaeaea;
    border-radius: 5px;
    color: white;
}

.Detail-View-Main-Content-Body > .stock-list > .stock-list-item > .stock-list-item-stock {
    font-size: 16px;
    font-family: 'Lexend Deca', sans-serif;
    padding: 3px;
    align-self: center;
    align-content: center;
    align-items: center;
    color: white;
}

.Detail-View-Main-Content-Body > .stock-list > .stock-list-item > .stock-list-item-type {
    font-size: 12px;
    font-family: 'Lexend Deca', sans-serif;
    padding: 3px;
    /*margin-bottom: 10px;*/
    align-self: center;
    align-content: center;
    align-items: center;
    color: white;
}

.Detail-View-Main-Content-Body > .stock-list > .stock-list-item > .stock-list-item-type > span {
    font-size: 10px;
    font-family: 'Roboto', sans-serif;
    font-style: italic;
    padding: 3px;
    /*margin-bottom: 5px;*/
    /*border: 1px solid #eaeaea;*/
    border-radius: 5px;
    color: white;
    text-decoration: underline;
    cursor: pointer;
}

.Detail-View-Main-Content-Body > .stock-list > .stock-list-item > .stock-list-item-type > span:hover {
    font-size: 10px;
    font-family: 'Roboto', sans-serif;
    font-style: italic;
    padding: 3px;
    /*margin-bottom: 5px;*/
    /*border: 1px solid #eaeaea;*/
    border-radius: 5px;
    color: #3235ff;
    text-decoration: underline;
}

.Detail-View-Main-Content-Body > .stock-list > .add {
    background-color: #4caf50;
    /*box-shadow: 0px 0px 2px 1px rgb(76, 175, 80);*/
}

.Detail-View-Main-Content-Body > .stock-list > .remove {
    background-color: #ff4648;
    /*box-shadow: 0px 0px 5px 1px rgba(255, 70, 72, 0.43);*/
}

.Detail-View-Main-Content-Body > .stock-list > .used {
    background-color: #ff925a;
    /*box-shadow: 0px 0px 5px 1px #ff925a;*/
}

.Detail-View-Main-Content-Body > .stock-list > .stock-list-item > .stock-list-item-added-by {
    font-size: 12px;
    font-family: 'Lexend Deca', sans-serif;
    padding: 3px;
    /*margin-bottom: 5px;*/
    /*border: 1px solid #eaeaea;*/
    border-radius: 5px;
    color: white;
}

.Detail-View-Main-Content-Body > .stock-list > .stock-list-item > .stock-list-item-added-by > span {
    font-size: 10px;
    font-family: 'Roboto', sans-serif;
    font-style: italic;
    padding: 3px;
    /*margin-bottom: 5px;*/
    /*border: 1px solid #eaeaea;*/
    border-radius: 5px;
    color: white;
}

.product-avg-usage-box {
    border-radius: 5px;
    background-color: #cfcfcf;
    margin: 20px;
}

@media screen and (max-width: 800px) {
    .Detail-View-Main-Content {
        display: grid;
        grid-template-columns: 100%;
        grid-template-rows: auto;
        border-bottom: 1px solid #eaeaea;
    }

    .Detail-View-Main-Content-Column1 {
        border-bottom: 1px solid #eaeaea;
        padding-bottom: 10px;
        margin-bottom: 10px;
    }

    .Detail-View-Main-Content-Column2 {
        border-left: 0px;
    }

    .Detail-View-Main-Content-Body > .stock-list {
        /*overflow-x: scroll;*/
        display: grid;
        grid-template-columns: 100%;
        grid-template-rows: auto;

    }
}