.auth-main {
    width: 100%;
    height: 100vh;
    display: grid;
    grid-template-columns: 20% 60% 20%;
    grid-template-areas:". banner .";
}

.auth-banner {
    width: 65%;
    grid-area: banner;
    align-self: center;
    justify-self: center;
    background-color: #eaeaea;
    padding: 20px;

    display: grid;
    grid-template-rows: auto;
    border-radius: 5px;
    grid-gap: 10px;
}

.auth-banner-logo {
    width: 75px;
    align-self: center;
    justify-self: center;
}

.auth-banner-app-name {
    font-size: 25px;
    font-family: 'Lexend Deca', sans-serif;
    color: #191d27;
    align-self: center;
    justify-self: center;
}

.auth-button {
    background: #191d27;
    width: 30%;
    border-radius: 4px;
    border: 0px;
    color: white;
    padding: 16px 50px;
    cursor: pointer;
    outline: none;
    font-family: 'Questrial', sans-serif;
    font-weight: bold;
    box-shadow: 0px 4px 20px 0px rgba(56, 56, 56, 0.5);
    transition: 0.3s background-color ease-in-out, 0.3s box-shadow ease-in-out;
    align-self: center;
    justify-self: center;
}

.auth-button:active{
    color: #191d27;
    background-color: rgba(255, 255, 255, 0.45);
    box-shadow: 0px 4px 20px 0px rgba(0, 0, 0, 0.05);
}

.auth-button:disabled{
    color: #e2e2e2;
    cursor: not-allowed;
    background-color: rgba(255, 255, 255, 0.45);
    box-shadow: 0px 4px 20px 0px rgba(0, 0, 0, 0.05);
}

@media screen and (max-width: 800px) {
    .auth-main {
        display: grid;
        grid-template-columns: 100%;
        grid-template-areas:"banner";
        background-color: white;
    }

    .auth-banner {
        width: auto;
        background-color: #eaeaea;
        border-radius: 5px;

        display: grid;
        grid-template-rows: auto;
        grid-gap: 5px;
    }

    .auth-banner-logo {
        width: 30px;
    }

    .auth-button {
        width: 100%;
    }
}