.app-view {
    width: 100%;
    height: 100vh;
    overflow: hidden;
    display: grid;
    grid-template-columns: 20% 80%;
    grid-template-areas: "appMenu appContent";
}

::-webkit-scrollbar {
    width: 0px;
    background: transparent;
}

.app-menu {
    background-color: #191d27;
    float: left;
    height: 100vh;
    padding-left: 10px;
    padding-right: 10px;
    z-index: 3;
    grid-area: appMenu;
}

.app-content {
    height: 100vh;
    overflow-y: auto;
    grid-area: appContent;
}

.app-page {
    display: grid;
    grid-template-rows: 75px calc(100vh - 75px);
    grid-template-areas:
            "pageHeader"
            "pageContent";
}

.page-content {
    overflow-y: auto;
    overflow-x: hidden;
}

.page-header {
    display: grid;
    grid-template-columns: 50% 50%;
    box-shadow: 0px 0px 5px 1px rgba(89, 89, 89, 0.16);
    border-bottom: 1px solid rgba(47, 47, 47, 0.2);
    overflow: hidden;
    height: 73px;
}

.page-header-name {
    padding-left: 10px;
    padding-right: 10px;
    color: black;
    font-family: 'Lexend Deca', sans-serif;
    align-self: center;
    font-weight: bold;
}

.page-header-add-btn {
    align-self: center;
    justify-self: end;
    padding-left: 10px;
    padding-right: 10px;
}

.app-name-container {
    padding-top: 10px;
    padding-bottom: 10px;
    display: grid;
    grid-template-columns: 30% 70%;
}

.app-header-icon {
    align-self: center;
    justify-self: center;
    background-image: url("./icon.png");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    width: 50px;
    height: 50px;
}

.app-header-icon > img {
    width: 50px;
    align-self: center;
    justify-self: center;
}

.app-header-name {
    align-self: center;
    font-family: 'Lexend Deca', sans-serif;
    font-size: 32px;
    color: #ffffff;
}

.app-header-menu-icon {
    display: none;
    padding-right: 10px;
}

.app-header-home-icon {
    padding-right: 10px;
}

.app-left-menu-items {
    display: block;
    background-color: #191d27;
    padding-top: 10px;
    z-index: 3;
    width: 100%;
}

.app-left-menu-list {
    display: grid;
    grid-template-columns: 80% 20%;
    height: 32px;
    padding: 10px;
    cursor: pointer;
    color: white;
    font-family: 'Lexend Deca', sans-serif;
}

.app-left-menu-list-active {
    background-color: #cdcdcd;
    border-radius: 10px;
    color: black;
}

.app-left-menu-name {
    align-self: center;
}

.app-left-menu-add-icon {
    align-self: center;
    justify-self: center;
}

.view-active {
    display: block;
}

.actual-content {
    height: calc(100vh - 75px);
    overflow-y: auto;
    overflow-x: hidden;
}

.app-actions {
    display: grid;
    grid-template-columns: 25% 25% 25% 25%;
    margin-top: 15px;
    margin-bottom: 30px;
}

.app-action {
    align-self: center;
    justify-self: center;
    cursor: pointer;
    padding-top: 10px;
    padding-bottom: 10px;
    padding-left: 15px;
    padding-right: 15px;
}

.app-action > svg {
    color: white;
}

.app-action-active {
    background-color: #cdcdcd;
    padding-top: 10px;
    padding-bottom: 10px;
    padding-left: 15px;
    padding-right: 15px;
    border-radius: 10px;
}

.app-action-active > svg{
    color: black;
}

.app-left-menu-group {
    background-color: #191d27;
    border-radius: 5px;
}

.app-left-menu-group-list {
    padding-top: 20px;
    padding-bottom: 20px;
}

.app-left-menu-branchName {
    font-family: 'Lexend Deca', sans-serif;
    padding-left: 10px;
    padding-right: 10px;
    padding-bottom: 15px;
    background-color: #191d27;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
    color: white;
    border-bottom: 1px solid #434343;
}

@media screen and (max-width: 800px) {
    .app-view {
        width: 100%;
        height: 100vh;
        overflow: hidden;
        display: grid;
        grid-template-columns: 100%;
        grid-template-rows: 50px calc(100vh - 50px);
        grid-template-areas:
                "appMenu"
                "appContent";
    }

    .app-menu {
        width: 100%;
        height: 50px;
        background-color: #191d27;
        padding-left: 10px;
        padding-right: 10px;
    }

    .app-name-container {
        display: grid;
        grid-template-columns: 10% 10% 80%;
    }

    .app-header-icon {
        align-self: center;
        justify-self: center;
        background-image: url("./icon.png");
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        width: 30px;
        height: 30px;
    }

    .app-header-name {
        align-self: center;
        font-family: 'Lexend Deca', sans-serif;
        font-size: 20px;
        color: #ffffff;
    }

    .app-content {
        width: 100%;
        height: 100vh;
    }

    .responsive-sec-header {
        display: none;
    }

    .app-left-menu-items {
        display: none;
        background-color: rgb(248, 248, 248);
        padding: 10px;
        border-radius: 3px;
        box-shadow: 0px 4px 20px 0px rgba(0, 0, 0, 0.5);
    }

    .app-left-menu-list {
        color: #191d27;
    }

    .app-header-menu-icon {
        display: flex;
        padding-right: 10px;
    }

    .app-action > svg {
        color: black;
    }

    .app-action-active {
        background-color: #191d27;
        padding: 10px;
        border-radius: 10px;
    }

    .app-action-active > svg{
        color: white;
    }

    .actual-content {
        height: calc(100vh - 125px);
    }

    .page-content {
        height: calc(100vh - 125px);
    }

}

@media only screen and (min-width: 800px) and (max-width: 1024px) {
    .app-view {
        grid-template-columns: 25% 75%;
    }
}

body::-webkit-scrollbar {
    width: 15px;
    border-radius: 0px;
    cursor: pointer;
}

body::-webkit-scrollbar-track {
    background: #f1f1f1;
}

body::-webkit-scrollbar-thumb {
    background: #888;
}

body::-webkit-scrollbar-thumb:hover {
    background: #555;
}

.actual-content::-webkit-scrollbar {
    width: 12px;
    border-radius: 0px;
}

.actual-content::-webkit-scrollbar-track {
    background: #f1f1f1;
}

.actual-content::-webkit-scrollbar-thumb {
    background: #888;
}

.actual-content::-webkit-scrollbar-thumb:hover {
    background: #555;
}