.dashboard-branch{
    padding: 10px;
}
.dashboard-branch-heading{
    /*padding: 10px;*/
    font-family: 'Lexend Deca', sans-serif;
    font-size: 20px;
    border-bottom: 1px solid #dddddd;
    padding-top: 10px;
    padding-bottom: 10px;
    padding-left: 10px;
    padding-right: 10px;
}

.dashboard-segment {
    padding: 10px;
}

.dashboard-stat-grid {
    display: grid;
    grid-template-columns: 20% 20% 20% 20% 20%;
    padding: 10px;
    /*grid-gap: 10px;*/
}

.dashboard-stat-card{
    margin: 10px;
    background-color: white;
    border-radius: 5px;
}

.dashboard-stat-inner {
    display: grid;
    grid-template-rows: auto;
    padding: 20px;
    justify-items: center;
    justify-content: center;
}

.dashboard-stat-heading {
    justify-self: center;
    align-self: center;
}

.dashboard-stat-value {
    font-size: 32px;
    justify-self: center;
    align-self: center;
}

.dashboard-stat-clickable {
    cursor: pointer;
}

.dashboard-stat-clickable:hover {
    background-color: #fafafa;
}

@media screen and (max-width: 800px) {
    .dashboard-stat-grid {
        grid-template-columns: 50% 50%;
    }

    .dashboard-stat-inner {
        padding: 10px;
    }

    .dashboard-stat-card {
        margin: 5px;
    }
}