.content-invoice-split-view-left {
    display: inline-block;
    float: left;
    width: 70%;
    height: calc(100vh - 57px);
    overflow-y: auto;
}

.invoice-left-search-li {
    background-color: white;
    border-radius: 5px;
    margin-top: 10px;
}

.content-invoice-split-view-left > div > .invoice-left-search-bar {
    /*padding: 10px;*/
    padding-top: 10px;
    padding-bottom: 10px;
}

.content-invoice-split-view-right {
    display: inline-block;
    float: right;
    width: 30%;
    height: calc(100vh - 57px);
    border: 1px solid #2f2f2f;
    /*background-color: #2f2f2f;*/
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
    position: relative;
}

.content-invoice-split-view-right > .invoice-right-header {
    font-size: 16px;
    font-family: 'Lexend Deca', sans-serif;
    color: white;
    padding-top: 10px;
    padding-bottom: 10px;
    padding-left: 5px;
    border-bottom: 1px solid #bcbcbc;
    background-color: #2f2f2f;
}

.content-invoice-split-view-right > .invoice-right-footer {
    border-top: 1px solid #bcbcbc;
    position: absolute;
    bottom: 0;
    width: 100%;
    background-color: #2f2f2f;
}

.content-invoice-split-view-right > .invoice-right-footer > .invoice-right-footer-header {
    font-size: 16px;
    font-family: 'Lexend Deca', sans-serif;
    color: white;
    padding-top: 20px;
    padding-bottom: 10px;
    padding-left: 5px;
    /*border-top: 1px solid #bcbcbc;*/
    width: 20%;
    display: inline-block;
    float: left;
}

.content-invoice-split-view-right > .invoice-right-footer > .invoice-right-footer-body {
    font-size: 32px;
    font-family: 'Lexend Deca', sans-serif;
    color: white;
    padding-top: 20px;
    padding-bottom: 20px;
    padding-right: 5px;
    width: 80%;
    display: inline-block;
    float: right;
}

.content-invoice-main {
    width: 100%;
    height: 100vh;
    overflow-y: auto;
}

.content-invoice-header {
    width: 100%;
    padding: 10px;
    vertical-align: middle;
    /*box-shadow: 0px 0px 5px 1px rgba(89, 89, 89, 0.16);*/
    z-index: 999;
    border-bottom: 1px solid rgba(47, 47, 47, 0.2);
}

.content-invoice-header-table {
    width: 100%;
}

.content-invoice-header-name {
    color: black;
    font-family: 'Lexend Deca', sans-serif;
}

.content-invoice-header-split {
    display: inline;
}

.content-invoice-header-right {

}

.content-invoice-split {
    display: inline-block;
    vertical-align: top;
}

.content-invoice-customer {
    display: grid;
    grid-template-columns: 20% 80%;
    grid-template-areas: "invoiceCustomer invoiceDate";
}

.content-invoice-customer-split {
    display: inline-block;
    vertical-align: top;
}

.content-invoice-customer-label {
    padding: 10px;
    font-family: 'Lexend Deca', sans-serif;
    font-size: 16px;
    text-align: right;
}

.content-invoice-customer-content {
    padding-left: 10px;
    /*border-left: 1px solid rgba(0, 0, 0, .15);*/
}

.content-invoice-customer-content-text {
    padding: 10px;
    font-family: 'Roboto', sans-serif;
    font-size: 16px;
    text-align: left;
}

.content-invoice-customer-content-text-box {
    border: 1px solid rgba(47, 47, 47, 0.2);
    border-radius: 3px;
}

.content-invoice-search-box {
    /*width: 100%;*/
    padding-left: 20px;
    padding-right: 20px;
}

.content-invoice-table {
    width: 100%;
    padding: 20px;
}

.ci-table {
    border-collapse: collapse;
}

.content-invoice-table .ci-table {
    width: 100%;
    table-layout: fixed;
    border-radius: 3px;
}

/*.content-invoice-table .ci-table, .ci-th, .ci-td {*/
