/*}*/

.content-invoice-add-product-service {
    padding: 15px;
    display: inline-block;
}

.content-invoice-add-product-service > span {
    display: inline-block;
    vertical-align: top;
}

.content-invoice-add-product-service > span:hover {
    cursor: pointer;
}

.content-invoice-add-product-service > .text {
    font-family: 'Lexend Deca', sans-serif;
    font-size: 16px;
    padding: 3px;
    color: #4b53ff;
}

.content-invoice-add-product-service > .text:hover {
    text-decoration: underline;
}

.invoice-src-type {
    display: inline-block;
}

.invoice-src-type-drop {
    width: 100%;
    vertical-align: top;
}

.invoice-src-type-checkbox {
    font-family: 'Lexend Deca', sans-serif;
    /*border: 1px solid #dddddd;*/
    margin-right: 20px;
    user-select: none;
}

.invoice-src-type-checkbox > input {
    margin-right: 5px;
    cursor: pointer;
}

.invoice-src-type-button-active {
    background-color: #4caf50;
    border: 1px solid #4caf50;
    color: white;
}

.content-invoice-total {
    margin: 20px;
    padding: 10px;
    /*float: right;*/
    border: 1px solid #dddddd;
    border-radius: 5px;
    bottom: 0;
    background-color: #f5f5f5;
}

.content-invoice-total > div {
    margin: 12px 5px 12px 5px;
}

.content-invoice-total > div > .amount {
    font-family: 'Lexend Deca', sans-serif;
    font-size: 20px;
    float: right;
    vertical-align: top;
    display: inline-block;
}

.content-invoice-total > div > .total-name {
    font-family: 'Roboto', sans-serif;
    /*padding-bottom: 10px;*/
    vertical-align: top;
    display: inline-block;
    font-weight: bold;
}

.typeLabel {
    font-family: 'Roboto', sans-serif;
    padding: 3px;
    margin-right: 10px;
    border-radius: 3px;
    background-color: #2f2f2f;
    font-size: 10px;
    letter-spacing: 2px;
    color: white;
}

.item-amount {
    font-family: 'Lexend Deca', sans-serif;
    font-size: 20px;
    margin-left: 5px;
}

.border-top {
    border-top: 2px solid #dddddd;
    padding-top: 10px;
}

.content-invoice-total > .border-top > .amount {
    font-family: 'Lexend Deca', sans-serif;
    font-size: 28px;
    float: right;
    vertical-align: top;
    display: inline-block;
}

.content-invoice-total > .border-top > .total-name {
    font-family: 'Lexend Deca', sans-serif;
    /*padding-bottom: 10px;*/
    vertical-align: top;
    display: inline-block;
    font-weight: bold;
}

.content-invoice-table-avg-product-used {
    width: 100%;
    background-color: white;
    border-radius: 5px;
    padding: 3px;
    margin-top: 3px;
}

.content-invoice-table-avg-product-used-action {
    float: right;
    cursor: pointer;
}

.content-invoice-table-close {
    cursor: pointer;
}

.invoice-detail {
    display: grid;
    grid-template-columns: 50% 50%;
    grid-template-areas: "invoiceCustomer invoiceDate";
    grid-gap: 10px;
    padding: 5px;
    margin: 20px;
}

.invoice-detail-customer {
    display: grid;
    grid-template-rows: auto auto;
    /*grid-gap: 10px;*/
}

.content-invoice-customer-grid {
    width: 90%;
    display: grid;
    grid-template-columns: 30% 80%;
    padding-top: 10px;
    padding-bottom: 10px;
}

.invoice-detail-date {
    align-self: center;
    justify-content: end;
}

.content-invoice-items {
    display: grid;
    grid-template-rows: auto;
    padding: 20px;
    grid-gap: 1px;
}

.content-invoice-item {
    display: grid;
    grid-template-columns: 35% 20% 15% 5% 20% 5%;
    grid-template-areas:"ciItemName ciItemQuantity ciItemPrice ciItemUnit ciItemTotal ciItemCancel";
    border-radius: 3px;
    border-top: 1px solid #dddddd;
    border-left: 1px solid #dddddd;
    border-bottom: 1px solid #dddddd;
    border-right: 1px solid #dddddd;
}

.ci-item {
    padding: 15px;
}

.ci-item-name {
    grid-area: ciItemName;
    /*justify-self: center;*/
    align-self: center;
    padding: 10px;
}

.ci-item-quantity {
    grid-area: ciItemQuantity;
    align-self: center;
    padding: 10px;
}

.ci-item-price {
    grid-area: ciItemPrice;
    justify-self: center;
    align-self: center;
    padding: 10px;
}

.ci-item-unit {
    grid-area: ciItemUnit;
    justify-self: center;
    align-self: center;
    padding: 10px;
}

.ci-item-total {
    grid-area: ciItemTotal;
    justify-self: center;
    align-self: center;
    padding: 10px;
}

.ci-item-cancel {
    grid-area: ciItemCancel;
    justify-self: center;
    align-self: center;
    cursor: pointer;
    padding: 10px;
}

.justify-center {
    justify-self: center;
    align-self: center;
}

@media screen and (max-width: 800px) {
    .invoice-detail {
        grid-template-columns: 100% 0%;
    }

    .invoice-detail-date {
        display: none;
    }

    .content-invoice-items {
        grid-gap: 10px 0px;
    }

    .content-invoice-item {
        display: grid;
        grid-template-columns: 50% 50%;
        grid-template-rows: auto;
        grid-template-areas:"ciItemName ciItemCancel""ciItemQuantity ciItemQuantity"". ciItemTotal";
    }

    .ci-item-cancel {
        grid-area: ciItemCancel;
        justify-self: end;
        align-self: center;
        cursor: pointer;
    }

    .ci-item-price {
        display: none;
    }

    .ci-item-unit {
        display: none;
    }

    .content-invoice-item-header {
        display: none;
    }
}