.content-header {
    width: 100%;
    padding: 10px;
    vertical-align: middle;
    box-shadow: 0px 0px 5px 1px rgba(89, 89, 89, 0.16);
    z-index: 999;
    border-bottom: 1px solid rgba(47, 47, 47, 0.2);
}

.content-header-table {
    width: 100%;
}

.content-header-name {
    color: black;
    font-family: 'Lexend Deca', sans-serif;
}

.content-header-split {
    display: inline;
}

.content-header-right {

}

.float-left {
    float: left;
}

.float-right {
    float: right;
}

