.content-container {
    width: 100%;
    padding: 10px;
}

.content-container-view {
    /*border-radius: 10px;*/
    background-color: #fafafa;
    overflow: hidden;
    /*box-shadow: 0px 0px 5px 1px rgba(89, 89, 89, 0.16);*/
}

.content-container-view > .content-container-view-table {
    border: 1px solid black;
    border-collapse: collapse;
}

.content-container-view > .content-container-view-th {
    border: 1px solid black;
    border-collapse: collapse;
}

.content-container-view > .content-container-view-td {
    border: 1px solid black;
    border-collapse: collapse;
}

.customer-li-main {
    padding: 10px;
    border-bottom: 1px solid #eaeaea;
    cursor: pointer;
}

.customer-li-deleted {
    padding-left: 3px;
    padding-right: 3px;
    border-radius: 3px;
    border: 1px solid #ea180e;
    font-size: 8px;
    margin-right: 10px;
    font-family: 'Lexend Deca', sans-serif;
}

.customer-li-main:hover {
    background-color: #eaeaea;
}

.customer-li-user-icon {
    width: 34px;
    padding: 3px;
    border: 1px solid #eaeaea;
    border-radius: 50%;
    background-color: white;
}

.customer-li-split {
    display: inline-block;
    vertical-align: top;
}

.customer-li-name {
    margin-left: 10px;
    font-size: 16px;
    width: 250px;
    font-family: 'Lexend Deca', sans-serif;
    overflow: hidden;
}

.customer-li-name > .mobile-number {
    font-size: 14px;
    padding-top: 10px;
    font-weight: lighter;
    font-family: 'Roboto', sans-serif;
}

.customer-li-address {
    margin-left: 10px;
    width: 300px;
    font-family: 'Roboto', sans-serif;
}

.customer-li-age-header {
    font-family: 'Lexend Deca', sans-serif;
    margin-right: 10px;
}

.customer-li-age-value {
    font-family: 'Roboto', sans-serif;
    padding-top: 10px;
    font-weight: lighter;
    font-size: 14px;
}

.customer-li-success-msg {
    padding: 10px;
    border-radius: 3px;
    margin: 10px;
    border: 1px solid #089058;
    background-color: white;
    font-family: 'Lexend Deca', sans-serif;
}

.customer-li-error-msg {
    padding: 10px;
    border-radius: 3px;
    margin: 10px;
    border: 1px solid #90212f;
    background-color: white;
    font-family: 'Lexend Deca', sans-serif;
}