.ModalIndex {

}

.Modal-Mini {
    width: 50%;
    border-radius: 10px;
    padding: 0px;
}

.Modal-Big {
    width: 100%;
    border-radius: 10px;
    padding: 0px;
    max-width: 1000px;
}

.ModalIndex > div > .Header {
    box-shadow: 0px 0px 5px 1px rgba(89, 89, 89, 0.16);
    font-family: 'Lexend Deca', sans-serif;
    font-size: 16px;
    /*position: absolute;*/
    width: 100%;
    z-index: 1000;
    background-color: white;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
}

.ModalIndex > div > .Header > div {
    padding: 15px;
}

.ModalIndex > div > .Header > div > span {
    font-family: 'Lexend Deca', sans-serif;
    font-size: 14px;
    font-style: italic;
}

.ModalIndex > div > .Content {
    /*padding-top: 60px;*/
    background-color: #f5f5f5;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
}

.ModalIndex > div > .Footer {
    float: right;
}

.ModalIndex > div > .Footer > div {
    padding: 15px;
}

@media screen and (max-width: 800px) {
    .Modal-Mini {
        width: 100%;
    }
}